
<template>
    <div class="map-body">
        <div class="map-box">
          <div id='mymap'></div>
        </div>
        <div class="map-operate">
          <div class="map-control">
            <!-- 运行控制 -->
            <div class="btn-operate">
              <i class="el-icon-video-pause" @click='setlushuType(1)'></i>
              <i class="el-icon-video-play" @click='setlushuType(0)'></i>
              <i class="el-icon-refresh" @click='setlushuType(2)'></i>

              <!-- 速度控制 -->
              <el-dropdown
                size="small"
                split-button
                type="primary"
                @command="handleCommand">
                播放速度
                <el-dropdown-menu slot="dropdown">
                  <el-dropdown-item command="10">慢</el-dropdown-item>
                  <el-dropdown-item command="50">中</el-dropdown-item>
                  <el-dropdown-item command="100">快</el-dropdown-item>
                </el-dropdown-menu>
              </el-dropdown>
            </div>
            <!-- 运行进度控制 -->
            <div class="map-slider">
              <el-slider
                v-model="startDistance"
                :show-tooltip="false"
                :max="lenPoint"
                @change="controlProgress">
              </el-slider>
            </div>
          </div>

          <!-- 实时速度 -->
          <div class="map-speed">
              <el-progress
                :width='120'
                type="dashboard"
                :percentage="currentSpeed"
                :format="format"></el-progress>
          </div>
        </div>
    </div>

</template>
<script>
// 引入第三方模块
import BMap from 'BMap'
import BMapLib from 'BMapLib'

// 行车轨迹数据
import trackPoints from '../js/track'
export default{
  data () {
    return {
      gpsArr: [], // 轨迹经纬度数组
      currentSpeed: 0, // 当前行车速度
      speed: 10, // 倍速
      lushu: {}, // 路书
      mycaiId: '陕D7356T', // 路书显示悬浮信息
      moveArrpois: [], // 路书需要数组 带有行车速度
      startDistance: 0, // 行车里程起始
      endDistance: 0, // 行车里程终点
      lenPoint: trackPoints.length,
      map: {}
    }
  },
  mounted () {
    this.initMap(trackPoints)
  },
  methods: {
    initMap (trackPoints) {
      let _this = this
      // 初始化生成Map实例
      this.map = new BMap.Map('mymap')
      this.map.centerAndZoom(new BMap.Point(117.878965, 28.385631), 18)
      this.map.enableScrollWheelZoom()
      // 画线
      console.log(trackPoints[trackPoints.length - 1])
      this.drawLine(trackPoints)
      this.drawLabel(trackPoints[0], 'https://api.map.baidu.com/img/markers.png')
      this.drawLabel(trackPoints[trackPoints.length - 1], 'https://api.map.baidu.com/img/markers.png')

      BMapLib.LuShu.prototype._moveNext = function (index) {
        let me = this
        // _path 就是路径节点
        // 开始行动节点
        // me._tween.linear 缓动效果处理方法
        _this.startDistance = index + 1
        if (index < this._path.length - 1) {
          me._move(me._path[index], me._path[index + 1], me._tween.linear)
        }
      }
      /**
      * 移动小车
      * @param {Point} initPos 经纬度坐标初始点.
      * @param {Point} targetPos 经纬度坐标目标点.
      * @param {Function} effect 缓动效果.
      * @return 无返回值.
      */
      BMapLib.LuShu.prototype._move = function (initPos, targetPos, effect) {
        // initPos和targetPos 都返回经纬度 和 速度
        _this.currentSpeed = initPos.speed
        let me = this
        // 当前的帧数
        let currentCount = 0
        // 步长 一秒走多少
        let timer = 5
        // 用每个初始点的速度计算行车速度
        // initPos.speed 行车速度
        // this._opts.speed 播放速度
        let step = initPos.speed * this._opts.speed / (1000 / timer)
        // 初始坐标 - 转换成坐标点
        // 经纬度坐标是球面坐标，所以要先转换为平面坐标
        let initPosition = this._projection.lngLatToPoint(initPos)
        // console.log(initPosition)
        // R {x: 13122162.975348942, y: 3277462.762315166}
        // 获取结束点的(x,y)坐标
        let targetPosition = this._projection.lngLatToPoint(targetPos)
        // 两点之间的距离
        let count = Math.round(me._getDistance(initPosition, targetPosition) / step)
        // 如果小于1直接移动到下一点
        if (count < 1) {
          me._moveNext(++me.i)
          return
        }
        // 两点之间匀速前进
        me._intervalFlag = setInterval(function () {
          // 两点之间当前帧数大于总帧数的时候，则说明已经完成移动
          if (currentCount >= count) {
            clearInterval(me._intervalFlag)
            // 移动的点已经超过总的长度
            if (me.i > me._path.length) {
              return
            }
            // 运行下一个点
            me._moveNext(++me.i)
          } else {
            // 节点之间的平滑运动
            currentCount++
            // console.log(count, currentCount)
            // 平面坐标pixel(x,y)。然后再将平面坐标转换为球面坐标给Marker进行定位
            let x = effect(initPosition.x, targetPosition.x, currentCount, count)
            let y = effect(initPosition.y, targetPosition.y, currentCount, count)
            let pos = me._projection.pointToLngLat(new BMap.Pixel(x, y))
            // 设置marker 每次节点进入另一个节点进行方向的设置
            if (currentCount === 1) {
              var proPos = null
              if (me.i - 1 >= 0) {
                proPos = me._path[me.i - 1]
              }
              // 设置车头转向
              if (me._opts.enableRotation === true) {
                me.setRotation(proPos, initPos, targetPos, me)
              }
            }
            if (me._opts.autoView) {
              // 判断是否在当前的可视区域
              // map.getBounds() 可视区域
              if (!me._map.getBounds().containsPoint(pos)) {
                me._map.setCenter(pos)
              }
            }
            // 正在移动 设置小车位置的移动
            me._marker.setPosition(pos)
            // 设置自定义overlay的位置  汽车头上的信息框
            me._setInfoWin(pos)
          }
        }, timer)
      }
      this.initLushu(this.map, this.moveArrpois)
    },

    drawLineColor (speed) {
      let lineColor = ''
      if (speed < 30) {
        lineColor = '#6edc46'
      } else if (speed > 30 && speed <= 60) {
        lineColor = '#ffbe1e'
      } else if (speed > 60 && speed <= 90) {
        lineColor = '#ff8732'
      } else if (speed > 90 && speed <= 120) {
        lineColor = '#ff4027'
      } else if (speed > 120) {
        lineColor = '#f00055'
      }
      return lineColor
    },

    drawLine (trackPoints) {
      let _this = this
      // 根据经纬度画线
      let arrPois = []
      let arrMovePosi = []
      for (let i = 0; i < trackPoints.length; i++) {
        arrPois.splice(0, arrPois.length)

        arrMovePosi.push(
          {
            lng: trackPoints[i].lon,
            lat: trackPoints[i].lat,
            speed: trackPoints[i].speed
          }
        )
        this.moveArrpois = arrMovePosi

        let p0 = trackPoints[i].lon
        let p1 = trackPoints[i].lat

        // 相邻两个点成线
        if (i + 1 < trackPoints.length) {
          var p01 = trackPoints[i + 1].lon
          var p11 = trackPoints[i + 1].lat
        }

        // 每次生成两个点
        arrPois.push(new BMap.Point(p0, p1))
        arrPois.push(new BMap.Point(p01, p11))

        // 循环去画线-轨迹
        var polyLine = new BMap.Polyline(arrPois, {
          strokeColor: _this.drawLineColor(trackPoints[i].speed), // 设置颜色
          strokeWeight: 5, // 宽度
          strokeOpacity: 0.8 // 透明度
        })
        this.map.addOverlay(polyLine)
      }
    },

    drawLabel (pos, url) {
      var myIcon = new BMap.Icon(url, new BMap.Size(23, 25), {
        offset: new BMap.Size(10, 25), // 指定定位位置
        imageOffset: new BMap.Size(0, 0 - 10 * 25) // 设置图片偏移
      })
      let Marker = new BMap.Marker({ lng: pos.lon, lat: pos.lat }, {icon: myIcon})
      this.map.addOverlay(Marker)
    },

    /**
     * 定义路书
     * @param {map} 地图对象
     * @param {arrPois} 路书轨迹
     */
    initLushu (map, arrPois) {
      this.lushu = new BMapLib.LuShu(map, arrPois, {
        defaultContent: this.mycaiId, // info
        autoView: true, // 是否开启自动视野调整，如果开启那么路书在运动过程中会根据视野自动调整
        icon: new BMap.Icon(
          'https://developer.baidu.com/map/jsdemo/img/car.png',
          new BMap.Size(52, 26),
          {
            anchor: new BMap.Size(27, 13) // 设置控件停靠的位置
          }
        ),
        speed: this.speed,
        landmarkPois: [],
        enableRotation: true // 是否设置marker随着道路的走向进行旋转
      })
      this.endDistance = this.lushu._path.length
    },

    /**
     * @param {index} 当前轨迹节点
     */
    controlProgress (index) {
      let lushu = this.lushu
      lushu.pause()
      lushu.i = index
      lushu._moveNext(index)
    },

    /**
     * @param {type} 操作类型
     * 0 开始
     * 1 暂停
     * 2 删除清空路书
     */
    setlushuType (type) {
      let lushu = this.lushu
      switch (type) {
        case 0:
          lushu.start()
          break
        case 1:
          lushu.pause()
          break
        case 2:
          lushu.stop()
          lushu = ''
          break
      }
    },

    /**
     * 改变行车速度
     * @param {Point} command 倍速
     */
    handleCommand (command) {
      // console.log(Number(command))
      this.lushu._opts.speed = Number(command)
    },
    format (percentage) {
      return `${percentage}km/h`
    }
  }
}

</script>
<style scoped>
  #mymap {
    width: 100%;
    height: 100%;
  }
  .map-operate {
    width: 100%;
    padding: 15px 0 15px 0;
    display: -webkit-flex;
    display: flex;
    justify-content: space-between;
  }
  .map-slider {
    width: calc(100% - 300px);
    padding-right: 15px;
  }
  .map-slider .el-slider{
    margin-top: -2px;
  }
  .map-control {
    height: 52px;
    display: -webkit-flex;
    display: flex;
    justify-content: space-between;
    margin-top: 165px;
    margin-left: 120px;
    border-radius: 32px;
    padding: 10px 10px 10px 10px;
    background: rgba(0, 0, 0, .26);
    width: calc(100% - 400px);
  }
  .map-speed {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 220px;
    height: 220px;
    background: #ffffff;
  }
  .btn-operate {
    width:300px;
    display: -webkit-flex;
    display: flex;
    justify-content: start;
  }
  .btn-operate i{
    margin: 0 5px;
    font-size: 28px;
    padding:4px;
  }
</style>



