<template>
    <div class="map-container">
        <div id="gedeoMapDemo"></div>
       <div class="input-card">
            <h4>轨迹回放控制</h4>
            <div class="input-item">
                <input type="button" class="btn" value="开始动画" id="start" @click="startAnimation()" />
                <input type="button" class="btn" value="暂停动画" id="pause" @click="pauseAnimation()" />
            </div>
            <div class="input-item">
                <input type="button" class="btn" value="继续动画" id="resume" @click="resumeAnimation()" />
                <input type="button" class="btn" value="停止动画" id="stop" @click="stopAnimation()" />
            </div>
        </div>
    </div>
</template>

<script>
import trackPoints from '../js/track'
import AMap from 'AMap'
import AMapUI from 'AMapUI'
export default {
  name: 'GaoDeMap',
  data () {
    return {
      data: [[116.478935, 39.997761], [116.478939, 39.997825], [116.478912, 39.998549], [116.478912, 39.998549], [116.478998, 39.998555], [116.478998, 39.998555], [116.479282, 39.99856], [116.479658, 39.998528], [116.480151, 39.998453], [116.480784, 39.998302], [116.480784, 39.998302], [116.481149, 39.998184], [116.481573, 39.997997], [116.481863, 39.997846], [116.482072, 39.997718], [116.482362, 39.997718], [116.483633, 39.998935], [116.48367, 39.998968], [116.484648, 39.999861]],
      markerCar: {},
      map: {},
      color: '',
      pathSimplifierIns: {}
    }
  },
  mounted () {
    AMapUI.load(['ui/misc/PathSimplifier'], function (PathSimplifier) {
      // 先判断是否支持
      if (!PathSimplifier.supportCanvas) {
        alert('当前环境不支持 Canvas！')
        return
      }

      // 启动地图初始化
      this.initPage(PathSimplifier)
    }.bind(this))
  },
  methods: {
    initPage (PathSimplifier) {
      // let _this = this
      // 地图初始化
      this.map = new AMap.Map('gedeoMapDemo', {
        resizeEnable: true,
        mapStyle: 'amap://styles/a8fdec119d6af8ed151ae83c97eff4d6', // 设置地图的显示样式
        zoom: 15, // 现实大小
        lang: 'en',
        center: [116.478935, 39.997761]// 中心位置
      })

      // 创建组件实例
      this.data = trackPoints.map(ele => {
        return [ele.lon, ele.lat]
      })
      for (var i = 0; i < this.data.length - 1; i++) {
        this.initPathSimplifierIns(trackPoints[i].speed, this.data[i], this.data[i + 1], PathSimplifier)
      }
    },

    initPathSimplifierIns (speed, posiPre, posiNext, PathSimplifier) {
      // console.log(this.drawLineColor(speed))
      let _this = this
      this.pathSimplifierIns = new PathSimplifier({
        zIndex: 100,
        map: _this.map, // 所属的地图实例
        getPath: function (pathData) {
          // 返回轨迹数据中的节点坐标信息，[AMap.LngLat, AMap.LngLat...] 或者 [[lng|number,lat|number],...]
          return pathData.path
        },
        getHoverTitle: function (pathData, pathIndex, pointIndex) {
          // 返回鼠标悬停时显示的信息
          if (pointIndex >= 0) {
            // 鼠标悬停在某个轨迹节点上
            return speed + '，点:' + pointIndex + '/' + pathData.path.length
          }
          // 鼠标悬停在节点之间的连线上
          return speed + '，点数量' + pathData.path.length
        },
        renderOptions: {
          // 轨迹线的样式
          pathLineStyle: {
            dirArrowStyle: true
          },
          getPathStyle: function (pathItem, zoom) {
            return {
              pathLineStyle: {
                strokeStyle: _this.drawLineColor(speed),
                lineWidth: 6
              },
              pathNavigatorStyle: {
                width: 16,
                height: 32,
                content: PathSimplifier.Render.Canvas.getImageContent('https://webapi.amap.com/images/car.png', onload, onerror),
                strokeStyle: null,
                fillStyle: null
              }
            }
          },
          // 绘制路线节点，如不需要可设置为-1
          renderAllPointsIfNumberBelow: 100
        }
      })
      // 设置数据
      this.pathSimplifierIns.setData([{
        name: '路线0',
        path: [posiPre, posiNext]
      }])
    },

    drawLineColor (speed) {
      let lineColor = ''
      if (speed < 30) {
        lineColor = '#6edc46'
      } else if (speed > 30 && speed <= 60) {
        lineColor = '#ffbe1e'
      } else if (speed > 60 && speed <= 90) {
        lineColor = '#ff8732'
      } else if (speed > 90 && speed <= 120) {
        lineColor = '#ff4027'
      } else if (speed > 120) {
        lineColor = '#f00055'
      }
      return lineColor
    },

    startAnimation () {
      let _this = this
      // 设置数据
      this.pathSimplifierIns.setData([{
        name: '路线0',
        path: _this.data
      }])
      // 创建一个巡航器
      var navg0 = this.pathSimplifierIns.createPathNavigator(0, {
        loop: false, // 循环播放
        speed: 5000
      })

      navg0.start()
      var timer = setInterval(() => {
        // 定时器需要提前声明
        // console.log(navg0.getCursor().idx)
        if (navg0.getCursor().idx === trackPoints.length - 1) {
          clearInterval(timer)
        }
        // console.log(trackPoints[navg0.getCursor().idx].speed)
        navg0.setSpeed(trackPoints[navg0.getCursor().idx].speed * 50)
      })
      // console.log(timer)
    }
  }
}

</script>

<style scoped>
   .map-container {
       height: 100%;
       width: 100%;
   }
   #gedeoMapDemo {
       height: 100%;
       width: 100%;
   }
    .input-card .btn{
        margin-right: 1.2rem;
        width: 9rem;
    }

    .input-card .btn:last-child{
        margin-right: 0;
    }
</style>



